<template>

	<div class="cart-form">
		<header class="section__header header">
			<h3>Данные заказчика</h3>
		</header>
		<form action="" class="form order-form cart-form__form">
			<div class="row">
				<div class="col-12 col-md-4">
					<div class="form__field">
						<input type="text" v-model="customer.name" name="name" placeholder="Имя">
					</div>
				</div>
				<div class="col-12 col-md-4">
					<div class="form__field">
						<input type="email" v-model="customer.email" name="email" placeholder="Email">
					</div>
				</div>
				<div class="col-12 col-md-4">
					<div class="form__field">
						<input type="text" v-model="customer.phone" name="phone" placeholder="Телефон">
					</div>
				</div>
			</div>
			<div>
				<div class="form__field">
					<input type="text" v-model="customer.address" name="address" placeholder="Адрес доставки">
				</div>
			</div>
		</form>
		<div class="cart-form__text">
			<p>
				Доставка курьером (г.Минск) или почтой (Беларусь) бесплатна. При доставке почтой c наложенным платежом + 3%, но не менее 10 тыс. рублей за электронный перевод денег. При доставке почтой с предварительной оплатой через ЕРИП (Расчет) дополнительный платеж не взымается.
			</p>
		</div>
		<div class="cart-form__items">
			<header class="section__header header">
				<h3>Заказ</h3>
			</header>
			<div class="cart-form-items tbl">
				<div class="tbl__row">
					<div class="tbl__cell tbl__cell_header">
						Наименование
					</div>
					<div class="tbl__cell tbl__cell_header">
						Тип
					</div>
					<div class="tbl__cell tbl__cell_header">
						Вес
					</div>
					<div class="tbl__cell tbl__cell_header">
						Цена
					</div>
				</div>
				<div v-for="(item, i) in cartItems" class="tbl__row">
					<div class="tbl__cell">
						<strong>
							<router-link :to="{name:'product-details', params:{id:item.product.id}}">
								{{item.product.name}}
							</router-link>
						</strong>
					</div>
					<div class="tbl__cell">
						{{item.type}}
					</div>
					<div class="tbl__cell">
						{{item.size * item.count / 1000}} кг.
					</div>
					<div class="tbl__cell">
						<strong>
							{{item.price}} руб.
						</strong>
					</div>
				</div>
			</div>
		</div>
		<a class="btn" @click="$router.go(-1)"><font-awesome-icon icon="arrow-left" class="btn__icon"/> Назад</a>
	</div>
	
</template>
<script>
	import Vue from 'vue'
	import {mapGetters} from 'vuex'

	export default {
		components:{
			
		},
		data(){
			return {
				customer:{
					name:'',
					phone:'',
					email:'',
					address:''
				}
			} 
		},
		methods:{
			returnToList(){
				this.$router.push('/')
			}
		},
		computed:{
			...mapGetters({
				cartItems:'getCartItems'
			}),
		}
	}
</script>
<style lang="scss">
	.form{
		&__field{
			margin-bottom: 20px;
		}
	}
	.cart-form-items{
		width: 100%;
	}
	.cart-form{
		&__form{
			margin-bottom: 40px;
		}
		&__text{
			margin-bottom: 40px;
		}
		&__items{
			margin-bottom: 40px;
		}
	}
	
</style>